import React from "react";
import "./SocialMediaIcons.css";

class SocialMediaIcons extends React.Component {
  componentDidMount = () => {
    window.addEventListener("scroll", this.icons);
  };
  icons = () => {
    const distanceY = window.pageYOffset || document.documentElement.scrollTop,
      colorIcons = window.innerHeight,
      github = document.getElementById("fa-github"),
      linkedin = document.getElementById("fa-linkedin-square"),
      email = document.getElementById("fa-envelope");
    if (distanceY > colorIcons) {
      github.classList.add("fa-color");
      linkedin.classList.add("fa-color");
      email.classList.add("fa-color");
    } else {
      github.classList.remove("fa-color");
      linkedin.classList.remove("fa-color");
      email.classList.remove("fa-color");
    }
  };
  render() {
    return (
      <React.Fragment>
        <div className="marg-icon">
          <div className="icon-wrapper">
            <a href="https://github.com/anAnun">
              <i className="fa fa-github" id="fa-github" />
            </a>
            <br />
            <a href="https://www.linkedin.com/in/allan-yeznaian/">
              <i className="fa fa-linkedin-square" id="fa-linkedin-square" />
            </a>
            <br />
            <a href="mailto:allanyeznaian@gmail.com">
              <i className="fa fa-envelope" id="fa-envelope" />
            </a>
            <br />
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default SocialMediaIcons;
