import React, { Component } from "react";
import "./App.css";
import { HashRouter, Route, Switch } from "react-router-dom";
import Home from "./Home";
import Resume from "./Resume";
import Mpp from "./Mpp";
import ReactGA from "react-ga";

function initializeReactGA() {
  ReactGA.initialize("UA-138962318-1");
  ReactGA.pageview("/homepage");
}
class App extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="background-image-app-wrapper">
          <div className="background-image-app" />
        </div>
        <HashRouter basename="https://ananun.github.io/allanyeznaian">
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/home" component={Home} />
            <Route exact path="/resume" component={Resume} />
            <div className="mpp">
              <Route exact path="/mpp" component={Mpp} />
            </div>
            <Route
              component={() => <div className="white-text">404 Not found</div>}
            />
          </Switch>
        </HashRouter>
      </React.Fragment>
    );
  }
}

export default App;
